img {
    position: absolute;
    left: -90%;
    height: 100%;
    width: auto;
    z-index: -1;
}

h1 {
    display: block;
    text-transform: uppercase;
    -webkit-align-self: center;
            align-self: center;
    text-align: center;
    font-size: 20px;
    font-family: 'Merriweather', serif;
    color: white;
