ul {
    display: block;
    position: absolute;
    -webkit-align-self: center;
            align-self: center;
    list-style: none;
    text-align: center;
    font-size: 46px;
    font-family: 'Fjalla One', sans-serif;
    -webkit-animation: showing_list 2s linear;
            animation: showing_list 2s linear;
    z-index: 1;
}

@-webkit-keyframes showing_list {

    0% {
        opacity: 0;
        /* top: 0%; */
    }

    100% {
        opacity: 1;
        /* top: 40%; */
    }

}

@keyframes showing_list {

    0% {
        opacity: 0;
        /* top: 0%; */
    }

    100% {
        opacity: 1;
        /* top: 40%; */
    }

}

a {
    display: block;
    text-transform: uppercase;
    text-decoration: none;
    color: white;
    transition: .5s;
}

a:hover {
    font-size: 52px;
}

a:active {
    font-size: 52px;
}

p {
